@import url(https://fonts.googleapis.com/css2?family=Reenie+Beanie&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Reenie+Beanie&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poltawski+Nowy:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poltawski+Nowy:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair:ital,wght@0,300;0,400;1,300&family=Poltawski+Nowy:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.navbar {
  height: 10vh;
}

a {
  text-decoration: none !important;
}

.footer {
  height: 10vh;
}

.App-header {
  background-image: url(/with-style/static/media/bg.jpg.0dd64a91.jpg);
  background-size: 130%;
  background-position: center;
  background-repeat: no-repeat;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  font-size: calc(10px + 2vmin);
  color: white;
}

.teaser-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-left: -20px;
}

.teaser-module {
  font-family: 'Reenie Beanie', cursive;
  background: black;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 300px;
  height: 340px;
  margin-right: 10px;
  text-decoration: none !important;
}

.teaser-module-image {
  border: 1px solid black;
  background: beige;
  width: 280px;
  height: 260px;  
  overflow: hidden;
}

.teaser-module-image img {
  width: 200px;
}

.teaser-wrapper .teaser-module h2 {
  font-family: 'Reenie Beanie', cursive;
  color: beige;
  text-decoration: none !important;
}

.teaser-module p {
  display: none;
}

.teaser-tile {
  width: 260px;
  height: 370px;
  border: 1px solid #282c34;
  color: #282c34;
  border-radius: 245px;
  padding: 10px 25px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background: #fff;
  overflow: hidden;
  text-decoration: none;
}

/* .teaser-tile img{
  max-height: 400px;
} */

.tile-1 .image-1,
.tile-2 .text-2,
.tile-3 .image-3 {
  display: none;
}


.tile-1:hover .image-1,
.tile-2:hover .text-2,
.tile-3:hover .image-3 {
  display: inline;
}

.tile-1:hover .text-1,
.tile-2:hover .image-2,
.tile-3:hover .text-3 {
  display: none;
}

.tile-1 {
  margin-right: -40px;
}

.tile-2 {
  margin-right: -20px;
}


@media only screen and (max-width: 750px) {
  .teaser-wrapper {
    flex-direction: column;
  }

  .tile-1,
  .tile-2,
  .tile-3 {
    margin: 10px 0 0 0;
  }
}

.tile-1 p,
.tile-2 p{
  margin-right: 25px;
}

.header-wrapper {
  width: 600px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.menu-wrapper {
  width: 300px;
  height: 50px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.menu-wrapper .menu-link{
  font-family:'Times New Roman', Times, serif;
  font-size: 20px;
  color: black;
}

.menu-wrapper .menu-link:hover{ 
  color: grey;
}

h1 {
  text-transform: uppercase;
  font-size: 45px !important;
  padding: 10px 0;
  font-weight: 600 !important;
  border-top: 1px solid black;
  border-bottom: 1px solid black;
  font-family:'Times New Roman', Times, serif;
  color: #000;
  margin-bottom: 100px;
  font-size: 100px;
}

h2 {
  font-family:'Times New Roman', Times, serif;
  font-size: 26px;
  color: #282c34;
}

.teaser-tile p {
  font-size: 16px;
}
.Article {
    /* background-image: radial-gradient(#ededed 10%, transparent 10%),
        radial-gradient(#ededed 10%, transparent 10%); */
    background-color: #fff;
    background-position: 0 0, 40px 40px;
    background-size: 40px 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.content-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 10px 55px;
    width: 70vw;
    max-width: 1000px;
    margin-top: 50px;
}

.inline-flexing {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: start;
}

.inline-flexing img{
    width: 150px;
    margin-left: 20px;
    /* max-width: 50%; */
}


.first-paragraph {
    margin: 40px 0;
   display: flex;
   flex-direction: row;
   justify-content: space-around;
   align-items: center;
   max-width: 1200px;
   font-weight: 600;
   text-align: center;
}

.first-paragraph div {
    padding: 20px;
    width: 50%;
    height: 70vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.first-paragraph .left {
    display: flex;
    flex-direction: column;
    justify-content: center;

    --b: 4px;   /* thickness of the border */
    --c: black;   /*color of the border*/
    --w: 20px;  /*width of border*/
    
  
    border: var(--b) solid #0000; 
    --_g: #0000 90deg,var(--c) 0;
    --_p: var(--w) var(--w) border-box no-repeat;
    background:
      conic-gradient(from 90deg  at top    var(--b) left  var(--b),var(--_g)) 0    0    / var(--_p),
      conic-gradient(from 180deg at top    var(--b) right var(--b),var(--_g)) 100% 0    / var(--_p),
      conic-gradient(from 0deg   at bottom var(--b) left  var(--b),var(--_g)) 0    100% / var(--_p),
      conic-gradient(from -90deg at bottom var(--b) right var(--b),var(--_g)) 100% 100% / var(--_p); 
}

.first-paragraph img {
    max-width: 500px;
    /* border-top: 2px solid black;
    border-bottom: 2px solid black; */

   
}

.more-button {
    border: 1px solid black;
    padding: 5px;
    margin-bottom: 10px;
    background: #fff;
}

.more-button:hover {
    background: #000;
    color: #fff;
}

.Article h1 {
    margin-top: 20px;
    text-transform: uppercase;
    text-align: center;
    font-size: 55px !important;
    padding: 10px 0;
    font-weight: 400 !important;
    /* border-top: 1px solid black;
    border-bottom: 1px solid black; */
    border:none;
    /* font-family: 'Times New Roman', Times, serif; */
    /* font-family: 'Poltawski Nowy', serif; */
    font-family: 'Playfair', serif;
    letter-spacing: -2.5px;
    line-height: 45px;
    color: #000;
    margin-bottom: 20px;
    font-size: 100px;   
}

.Article h2 {
    /* font-family: 'Harry Potter', sans-serif; */
    /* font-family: 'Times New Roman', Times, serif; */
    font-family: 'Poltawski Nowy', serif;
    /* font-family: 'Playfair', serif; */
    font-weight: 400;
    font-size: 30px;
    margin: 20px 0;
}

.Article h3 {
    /* font-family: 'Times New Roman', Times, serif; */
    font-size: 15px;
    font-weight: bold;
}

.content-wrapper img {
    /* max-width: 400px;
    max-height: 400px; */
    width: 150px;
    margin-right: 30px;
}

.image-wrapper {
    display: flex;
    flex-direction: column;
    width: 50%;
    justify-content: space-between;
    align-items: center;
    margin: 20px 0;
}

.image-wrapper .image {
    margin-top: 50px;
    text-align: center;
    /* border-top: 2.5px solid black;
    border-bottom: 2.5px solid black; */
}

.image-wrapper .image img {
    --b: 2px;   /* thickness of the border */
    --c: black;   /*color of the border*/
    --w: 20px;  /*width of border*/
    
  
    border: var(--b) solid #0000; 
    --_g: #0000 90deg,var(--c) 0;
    --_p: var(--w) var(--w) border-box no-repeat;
    background:
      conic-gradient(from 90deg  at top    var(--b) left  var(--b),var(--_g)) 0    0    / var(--_p),
      conic-gradient(from 180deg at top    var(--b) right var(--b),var(--_g)) 100% 0    / var(--_p), 
       conic-gradient(from 0deg   at bottom var(--b) left  var(--b),var(--_g)) 0    100% / var(--_p),
      conic-gradient(from -90deg at bottom var(--b) right var(--b),var(--_g)) 100% 100% / var(--_p); 
}


.image-wrapper img {
    max-width: 180px;

}

.image-wrapper .image p{
    font-size: 12px;
}

.article-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 1200px;
}

.article-wrapper .image-col {
    display: flex;
    flex-direction: column;
    max-width: 300px;
}




.article-wrapper .text-col {
    display: flex;
    flex-direction: column;
    max-width: 600px;
}

.article-wrapper .text-col h2 {
    margin-top: 20px;
}



@media only screen and (max-width: 750px) {
    .content-wrapper {
        flex-direction: column;
        width: 100vw;
        padding: 10px 10px;
    }

    .content-wrapper img{
        height: auto;
        width: 80vw;
        max-width: 200px;
    }
}
.About {
    background-color: pink;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:space-around;
}
